
.head {
  color: rgb(112, 175, 18);
  background-color: rgb(48, 47, 47);
  display: block;
  font-style: italic;
  font-size: 46px;
  font-weight: bold;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left:16px;
  border-bottom: 1px rgb(80, 128, 9) solid;
  
}

.search-sec{
  display: block;
  padding-bottom: 10px;
  border-bottom: 1px rgb(80, 128, 9) solid;
  background-color: rgb(32, 32, 32);
  text-align: center;
}

.search-bar{
  background-color: rgb(48, 47, 47);
  width: 50%;
  margin-top: 12px;
  padding-left: 16px;
  padding-top: 8px;
  padding-bottom: 8px;
  font-size: 16px;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  border: none;
  color: aliceblue;
  
}

.search-bar:focus {
  outline: none;
}

.button{
  display: inline;
  padding-top: 11px;
  padding-bottom: 10px;
  margin: 0px;
  outline: none;
  border: none;
  background-color: rgb(112, 175, 18);
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  color:honeydew;
}


.movie-container{
  display: flex;
  flex-wrap: wrap;
  padding-top:2rem;
  align-items: center;
  justify-content: center;
}

.movie{
  background-color: rgb(48, 47, 47);
  border-radius: 3px;
  box-shadow: 3px 3px 5px rgb(0,0,0,0.1);
  overflow: hidden;
  position: relative;
  color: white;
  margin: 1rem;
  width:250px;
}

.movie img{
  
  height: 300px;
  width: 100%;
}

.movie-info{
  display:flex;
  align-items: center;
  justify-content: space-between;
  padding:1rem;
  font-size: small;
  max-height: 30px;
}

.movie-info h3{
  margin: 0;
}

.movie-over{
  background-color: rgb(48, 47, 47);
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  box-shadow: 3px 3px 5px rgb(0,0,0,0.1);
  color: white;
  padding: 1rem;
  text-align: center;
  position:absolute;
  bottom:0;
  left:0;
  right:0;
  overflow: auto;
  max-height: 100%;
  transform: translateY(102%);
  transition: transform 0.5s ease-in-out;
}

.movie:hover .movie-over{
  transform: translateY(0%);
}

.tag{
  background-color: rgb(32, 32, 32);
  border-radius: 5px;
  font-weight: bold;
  padding:0.5rem;
}

.tag.green{
  color: green;
}

.tag.yellow{
  color: rgb(170, 170, 9);
}

.tag.red{
  color: rgb(168, 14, 14);
}
